<template>
    <div class="slideshow deafult-p">
        <div class="slideshow--scene">
            <div class="slideshow--images">
                <div v-for="(image, index) in images" :key="index"
                        :class="computeClass(index)">
                    <img 
                        :src="image"
                    >
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import img1 from '@/assets/bg__home.png'

export default {
    name: 'Slideshow',
    data() {
        return {
            images: [img1, img1, img1],
            show: 0,
            hide: null,
            interval: null
        }
    },
    methods: {
        computeClass: function(index) {
            return this.show == index ? 'show' : this.hide == index ? 'hide' : ''
        },
        changeSlide() {

            let increase = x => {
                return x + 1 == this.images.length ? 0 : x + 1
            }

            this.hide = this.show
            this.show = increase(this.show)
        },
    },
    created() {
        // setInterval(this.changeSlide(), 1000)

        this.interval = setInterval(() => {this.changeSlide()}, 5000)
    }
}
</script>

<style>
.slideshow {
    grid-column: 1;
}

.slideshow--scene {
    margin: 2rem;
    overflow: hidden;
    display: block;
}

.slideshow--images {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
}

.slideshow--images div {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    transition: clip-path .6s ease;
    background-color: red;
}
.slideshow--images div:nth-child(1) {
    filter: hue-rotate(180deg);
}

.slideshow--images div:nth-child(2) {
    filter: hue-rotate(270deg);
}

.slideshow--images div.hide {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
    z-index: 3;
}

.slideshow--images div.show {
    z-index: 2;
}

.slideshow--images div img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    object-fit: cover;
}
</style>